<template>
	<picker
		class="picker"
		:style="{
			'min-width': width + 'rpx'
		}"
		:range="range"
		@change="$emit('input',$event.detail.value)">
		<view>
			<text class="value">
				{{value}}
			</text>
			<text class="iconfont icon-xiala"></text>
		</view>
	</picker>
</template>

<script>
export default {
	data() {
	},
	props:{
		width: {
			type: Number,
			default: 200
		},
		range: {
			type: Array,
			default: () => []
		},
		value: {
			type: String,
			default: ""
		},
	}
}
</script>

<style lang="stylus" scoped>
.picker
	position relative
	padding 5rpx 15rpx
	border-radius 15px
	background-color var(--origin2)
	view
		display flex
		align-items center
	.value
		flex 1
		font-size 30rpx
	.iconfont
		margin 0 5px
		color var(--origin4)
</style>
